import draggable from 'vuedraggable';
//import vSelect from 'vue-select'
import Multiselect from 'vue-multiselect'

export default {
	name: 'vue-fields',
	props: ['settings'],
	data: function () {
		return {
			allFields: [],
			fields: [],
			cols: [
				{
					'code': 'name',
					'view': 'text'
				}
			],
			title: '',
			propertyID: '',
			lang: {
				clear: 'clear',
				add: 'add field',
				noResult: 'no result',
			}
		}
	},
	computed: {
		filterAllFields: function () {
			return this.allFields.filter( allFieldItem => {
				return this.fields.filter(fieldItem => {
					return allFieldItem.code === fieldItem.code
				}).length === 0;
			});
		},
		errorFields: function () {
			return this.fields.reduce( (ar, startField, startIndex) => {
				let doubleFields = this.fields.filter( (endField, endIndex) => {
					return startIndex !== endIndex && startField.code === endField.code
				});
				if (doubleFields.length) ar.push(doubleFields[0]['code']);
				return ar;
			}, []);
		},
		jsonFields: function () {
			return JSON.stringify(this.fields, '', 4);
		}
	},
	methods: {
		/**
		 * clone and delete reactive
		 */
		cloneData: function (data) {
			var self = this;
			var skipProperties = ['__ob__', 'reactiveGetter', 'reactiveSetter'];
//array
			if (Array.isArray(data)) {
				var cloneArray = [];
				data.forEach(function (dataArrayItem, dataArrayIndex) {
					if (skipProperties.indexOf(dataArrayIndex) > -1) return;
					cloneArray[dataArrayIndex] = self.cloneData(dataArrayItem);
				});
				return cloneArray;
			}
//object
			if (typeof data === 'object' && data.toString && data.toString() == "[object Object]") {
				var cloneObject = {};
				var dataObjectItemKey;
				for (dataObjectItemKey in data) {
					if (skipProperties.indexOf(dataObjectItemKey) > -1) continue;
					cloneObject[dataObjectItemKey] = this.cloneData(data[dataObjectItemKey]);
				}
				return cloneObject;
			}
			return data;
		},
		addField: function (field) {
			this.fields.push(this.cloneData(field));
		},
		removeField: function (fieldKey) {
			this.$delete(this.fields, fieldKey);
		},
		setFieldValue: function (e, fieldIndex, colCode) {
			this.fields[fieldIndex][colCode] = e.target.value;
		}
	},
	created: function () {
		this.allFields = this.settings.data.fields || [];
		
		// title
		this.title = this.settings.propertyParams.NAME;
		
		if (this.settings.data.lang)
			this.lang = this.settings.data.lang;
		
		if (this.settings.data.cols)
			this.cols = this.settings.data.cols;
		
		this.fields = Array.isArray(this.settings.data.value) && this.settings.data.value.length ?
			this.settings.data.value :
			(Array.isArray(this.settings.propertyParams.DEFAULT) ? this.settings.propertyParams.DEFAULT : []);
		this.propertyID = this.settings.propertyID;
	},
	mounted: function () {
	
	},
	components: {
		draggable,
		Multiselect
	}
}


