import settingsFields from './SettingsFields/index.vue'

if (typeof Vue.component('settings-fields') === 'undefined')
	Vue.component('settings-fields', settingsFields);

window.CitrusFieldSettings = function(settings) {
	let input = settings.oInput;
	
	let div = document.createElement('div');
	settings.oCont.appendChild(div);
	
	// set col full width
	input.parentNode.colSpan = 2;
	// remove first col
	if (input.parentElement.previousSibling)
		input.parentElement.previousSibling.remove();
	
	input.remove();
	
	new Vue({
		el: div,
		data: {settings: settings},
		template: '<settings-fields :settings="settings" />',
	});
};


